import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
// import adCell from '@/component/ADCell/ADCell.vue';
import { post, tokenpost } from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue';
export default {
	components: {
		// adCell
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '我的',
			changestatus:0,
			login_status: false,
			dataList: {
				loginname: '',
				realname: '',
				telephone: '',
				integral:0,
				companyname: '',
				expiredate: '',
				daycount: 0,
				ordercount: '0',
				ismain:0
			}
		};
	},
	onLoad() {
		uni.$on('changecompany', this.loadInit)
		
	},
	onShow() {
		this.loadInit();
		this.login_status = this.$api.login_status();
		//this.loadData();
	},
	onUnload(){
		uni.$off('changecompany')
	},
	methods: {
		loadInit(){
			this.tokenRefresh();
			this.loadData();
		},
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handlePassword() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/login/editPassword'
			});
		},
		handleBankSet() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/account/accountlist'
			});
		},
		handleAbout() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/about/about'
			});
		},
		handleRecharge() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/recharge/recharge'
			});
		},
		handleUserManage() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/user/user'
			});
		},
		handleTime() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/givetime'
			});
		},
		handleIntegral() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/integral/integral'
			});
		},
		handleShare() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '/pages/my/share/share'
			});
		},
		handlewx() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes);
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes);
						}
					});
				}
			});
		},
		//退出登录
		handleLogout() {
			uni.showModal({
				title: '确定要退出登录么？',
				success: e => {
					if (e.confirm) {
						try {
							uni.clearStorageSync();
							setTimeout(() => {
								const userInfo = uni.getStorageSync('userInfo');
								console.log(userInfo);
								if (!userInfo) {
									uni.reLaunch({
										url: '/pages/my/my'
									});
								}
							}, 200);
						} catch (e) {}
					}
				}
			});
		},
		//设置
		handleSet() {
			if (!this.login_status) {
				this.$api.login();
				return;
			}
			uni.navigateTo({
				url: '../my/set'
			});
		},
		tokenRefresh() {
			const userInfo = uni.getStorageSync('userInfo');
			tokenpost(api.tokenRefresh)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						console.log(userInfo.token);
						let refresh_userInfo = {
							token: res.data.data.token,
							exp: res.data.data.exp,
							userId: res.data.data.userId
						};
						uni.setStorage({
							key: 'userInfo',
							data: refresh_userInfo,
							success: function() {}
						});
					} else {
						//this.$api.msg('token刷新失败');
					}
				})
				.catch(error => {
					//this.$api.msg('token刷新失败');
				});
		},
		loadData() {
			this.$refs.loading.open()
			tokenpost(api.GetUserInfo)
				.then(res => {
					if (res.status == 200) {
						this.$refs.loading.close()
						if (res.data.returnCode == '0000') {
							this.dataList = res.data.data;
							this.login_status = true;
							if(this.dataList.companyname ==""){
								uni.navigateTo({
									url: '../my/set'
								});
							}
						} else {
							//this.$api.msg(res.data.returnMessage);
							this.dataList = { loginname: '', realname: '', telephone: '', companyname: '', expiredate: '', daycount: 0, ordercount: '0' };
							this.login_status = false;
						}
					} else {
						this.$api.msg(res.data.returnMessage);
					}
				})
				.catch(error => {
					this.$refs.loading.close()
					this.$api.msg('请求失败fail');
				});
		}
	}
};
